.catalog {
  padding: 50px 40px;
  display: grid;
  justify-content: center;
  gap: 46px 48px;
}

@include media-mixin(1px, catalog, grid-template-columns, min-content);
@include media-mixin(
  488px,
  catalog,
  grid-template-columns,
  repeat(2, min-content)
);
@include media-mixin(
  768px,
  catalog,
  grid-template-columns,
  repeat(3, min-content)
);
@include media-mixin(
  1024px,
  catalog,
  grid-template-columns,
  repeat(4, min-content)
);
